<template>
  <section class="withdrawals">
    <header class="withdrawals__header">
      <Heading>{{ t('withdrawals.title') }}</Heading>
    </header>
    <main class="withdrawals__container">
      <Tabs
        v-model="activeTab"
        class="withdrawals__container__tabs"
        :data="tabs"
        inactive-opacity="0.5"
        @update:model-value="changeTab"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </Tabs>
      <keep-alive>
        <component
          :is="activeTab.component"
        />
      </keep-alive>
    </main>
  </section>
</template>

<script>
import { computed, defineAsyncComponent, onBeforeUnmount, ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'
import { useI18n } from '@/shared/plugins/i18n'
import Heading from '@/admin/components/Heading'
import Tabs from '@/shared/components/UIKit/Tabs'
import withdrawalsModule from '@/admin/store/modules/withdrawals'
import { MODULES_NAMES } from '@/shared/common/modules'

export default {
  name: 'Withdrawal',
  components: {
    Tabs,
    Heading,
    AddNewWithdrawal: defineAsyncComponent(() => import('@/admin/views/treasury/Withdrawals/Tabs/AddNewWithdrawal')),
    WithdrawalHistory: defineAsyncComponent(() => import('@/admin/views/treasury/Withdrawals/Tabs/WithdrawalHistory'))
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    const { t } = useI18n()
    const tabs = computed(() => [
      { title: t('withdrawals.new'), key: 'new', icon: 'plus', component: 'AddNewWithdrawal' },
      { title: t('common.history'), key: 'history', icon: 'stack', component: 'WithdrawalHistory' }
    ])
    const activeTab = ref(tabs.value.find(el => el.key === route.query.t) || tabs.value[0])

    if (!store.hasModule(MODULES_NAMES.WITHDRAWALS)) {
      store.registerModule(MODULES_NAMES.WITHDRAWALS, withdrawalsModule)
    }
    onBeforeUnmount(() => {
      if (store.hasModule(MODULES_NAMES.WITHDRAWALS)) {
        store.unregisterModule(MODULES_NAMES.WITHDRAWALS)
      }
    })

    const changeTab = (tab) => {
      activeTab.value = tab
      router.replace({ ...route, query: { t: tab.key } })
    }

    return {
      t,
      tabs,
      activeTab,
      changeTab
    }
  }
}
</script>

<style lang="scss">
.withdrawals {
  padding: 30px 40px;

  &__header {
    margin-bottom: 40px;
  }

}
</style>
