import { data, columns } from '@/admin/store/mock/withdrawalsMock'

export default {
  namespaced: true,
  state: {
    historyTable: {
      columns: [],
      data: [],
      perPage: 20,
      page: 1,
      totalCount: 4
    }
  },
  mutations: {
    SET_HISTORY_DATA (state, payload) {
      state.historyTable.data = payload
    },
    SET_HISTORY_COLUMNS (state, payload) {
      state.historyTable.columns = payload
    },
    SET_HISTORY_PER_PAGE (state, payload) {
      state.historyTable.perPage = payload
    },
    SET_HISTORY_PAGE (state, payload) {
      state.historyTable.page = payload
    },
    SET_HISTORY_TOTAL_COUNT (state, payload) {
      state.historyTable.totalCount = payload
    }
  },
  actions: {
    setMockHistoryData ({ commit }) {
      commit('SET_HISTORY_DATA', data)
      commit('SET_HISTORY_COLUMNS', columns)
    }
  },
  getters: {
    getHistoryData (state) {
      return state.historyTable.data
    },
    getHistoryColumns (state) {
      return state.historyTable.columns
    },
    getHistoryPerPage (state) {
      return state.historyTable.perPage
    },
    getHistoryPage (state) {
      return state.historyTable.page
    },
    getHistoryTotalCount (state) {
      return state.historyTable.totalCount
    }
  }
}
